.state{
    font-size: 25px;
    padding: 1rem;
    background-color: rgb(117, 76, 76);
    color: white;
}

.video{
    height: 25rem;
    display: inline;
}

.contain{
    height: calc(100vh - 5rem);
    padding: 2rem;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1rem;
    overflow: hidden;
}

.videoContainer{
    max-width: 100%;
    border-radius: 0.5rem;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.781);
    background-blend-mode: multiply;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}

.videoContainer video{
    display: none;
    height: calc(100% - 2rem);
}

.videoContainer p{
    text-align: center;
    color: rgb(175, 175, 175);
    font-size: 18px;
    height: 2rem;
    width: 100%;
    background-color: white;
    padding: 0;
    margin: 0;
}


.hidding-block{
    height: 100%;
    width: 100%;
    display: none;
    background-color: rgba(0, 0, 0, 0.24);
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    padding: 3rem 2rem 3rem 2rem;
}

.hidding-block-content{
    height: 50rem;
    width: 50rem;
    background-color: white;
    border-radius: 0.5rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 100%;
    max-height: 100%;
    min-height: 600px;
}


.control{
    height: 5rem;
    background-color: rgb(41, 41, 41);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

i{
    color: white;
    font-size: 25px;
    border-radius: 50%;
}

.bi-x{
    color: black!important;
    margin: 0;
    padding: 0;
    font-size: 30px!important;
}

.chat-container{
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.24);
    position: absolute;
    top: 0;
    padding: 1rem;
    display: none;
    justify-content: flex-end;
}

.chat{
    height: 100%;
    width: 25%;
    min-width: 400px;
    background-color: white;
    padding: 2rem;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.content-container{
    height: 85%;
}

.content{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    font-weight: bold;
    overflow: auto;
}

.member-container{
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.24);
    position: absolute;
    top: 0;
    padding: 1rem;
    display: flex;
    justify-content: flex-end;
    display: none;
}

.member{
    height: 100%;
    width: 25%;
    min-width: 400px;
    background-color: white;
    padding: 2rem;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.member-content{
    display: flex;
    flex-direction: row;
    margin-top: 1rem;
    margin-bottom: 1rem;
    align-items: center;
    justify-content: space-between;
    height: 3rem;
}

.member-name{
    display: inline;
    font-size: 18px;
    margin-bottom: 0!important;
}

.member-image{
    background-size: cover;
    border-radius: 2rem;
    height: 2rem;
    width: 2rem;
    background-repeat: no-repeat;
    display: inline-block;
}

.bi-arrows-fullscreen{
    font-size: 18px;
}

.bi-arrows-fullscreen:hover{
    color: gray;
}
